.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: hsl(7, 20%, 60%);
  height: 8rem;
  width: 89%;
  padding-left: 5.5%;
  padding-right: 5.5%;
  margin-bottom: 50px;
}
.header__logo {
  font-size: 3rem;
  color: hsl(14, 65%, 9%);
  font-weight: 700;
}
.header__logo:hover {
  font-size: 3.1rem;
}
.header__navegacion {
  width: 50%;
}
.header__navegacion ul {
  display: flex;
  justify-content: end;
  list-style: none;
  gap: 2rem;
  padding: 0;
  width: 100%;
}
.header__enlace, .header__submit {
  color: white;
  font-size: 1.2rem;
  font-weight: 600;
}
.header__enlace:hover, .header__submit:hover {
  cursor: pointer;
  color: hsl(14, 86%, 42%);
}
.header__submit {
  background-color: transparent;
  border: none;
}
.header__admin {
  display: flex;
}
.header__admin .admin__fecha {
  display: flex;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: hsl(7, 20%, 60%);
  height: 60px;
  padding-left: 50px;
  padding-right: 50px;
  margin-top: 70px;
}

.login,
.registro {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.formulario {
  min-width: 350px;
  width: 30%;
  margin-bottom: 20px;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.formulario__label {
  margin-bottom: 10px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.5px;
  font-size: 1.25rem;
}
.formulario__input {
  border-radius: 0.25rem;
  height: 3rem;
  border: 1px solid hsl(7, 20%, 60%);
  padding-left: 1rem;
  font-size: 1.2rem;
}
.formulario__input::placeholder {
  color: hsl(14, 25%, 72%);
  font-size: 1.2rem;
}
.formulario__input--file {
  padding-left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 32px;
}
.formulario__input--img {
  display: flex;
  flex-direction: column;
}
.formulario__input-button {
  padding-left: 1rem;
  padding-right: 1rem;
  line-height: 3rem;
  width: 105px;
  flex: 0 0 auto;
  height: 100%;
  white-space: nowrap;
  border-right: 1px solid hsl(7, 20%, 60%);
}
.formulario__input-button:hover {
  cursor: pointer;
  font-weight: 600;
}
.formulario__input-text {
  flex: 1 1 auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.formulario__submit {
  height: 2.5rem;
  width: 10rem;
  text-transform: uppercase;
  color: white;
  font-weight: 600;
  background-color: hsl(14, 86%, 42%);
  border-radius: 50px;
  border: none;
}
.formulario__submit:hover {
  cursor: pointer;
  background-color: hsl(14, 86%, 32%);
}
.formulario__img {
  width: 200px;
  height: auto;
  border-radius: 15px;
}
.formulario--admin {
  width: 50%;
  max-width: 750px;
}
.formulario--admin .formulario__label {
  color: #1f2933;
}
.formulario--admin .formulario__input {
  border: 1px solid #dce3ec;
  color: #333333;
}
.formulario--admin .formulario__input::placeholder {
  color: rgb(146.8808510638, 152.7936170213, 164.6191489362);
  font-size: 1.2rem;
}
.formulario--admin .formulario__input-button {
  border-right: 1px solid #dce3ec;
  background-color: #dce3ec;
}
.formulario--admin .formulario__input-button:hover {
  background-color: rgb(192.6136363636, 209.25, 225.8863636364);
}
.formulario .boton {
  margin-bottom: 20px;
}

.acciones {
  display: flex;
  min-width: 325px;
  width: 30%;
  justify-content: space-between;
  gap: 5px;
}
.acciones--centrar {
  justify-content: center;
}
.acciones__enlace {
  color: hsl(14, 86%, 42%);
}
.acciones__enlace:hover {
  color: hsl(14, 65%, 9%);
}

.alerta {
  box-sizing: border-box;
  min-width: 325px;
  width: 30%;
  margin-bottom: 20px;
  padding: 10px 1rem;
  color: white;
  font-weight: 600;
  font-size: 17px;
  text-transform: uppercase;
  border-radius: 7px;
}
.alerta__error {
  background-color: rgb(169, 0, 0);
}
.alerta__exito {
  background-color: rgb(1.5384615385, 168.4615384615, 1.5384615385);
}

.tabla {
  border-collapse: collapse;
  margin-bottom: 50px;
}
.tabla__header, .tabla a {
  color: #333333;
}
.tabla__fila {
  height: 45px;
}
.tabla__celda--no-wrap {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.tabla__celda img {
  height: 100px;
  vertical-align: middle;
  border-radius: 8px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.tabla--admin .tabla__fila:hover:not(.tabla__fila--no-hover) {
  cursor: pointer;
}
.tabla--admin .tabla__celda {
  font-size: 25px;
  text-align: center;
  padding-left: 15px;
  padding-right: 15px;
}
.tabla--admin .tabla__celda--head:hover:not(.tabla__celda--no-hover) {
  background-color: rgb(192.6136363636, 209.25, 225.8863636364);
}
.tabla--admin .tabla__body .tabla__fila:nth-child(odd) {
  background-color: #dce3ec;
}
.tabla--admin .tabla__body .tabla__fila:hover:not(.tabla__fila--no-hover) {
  background-color: rgb(192.6136363636, 209.25, 225.8863636364);
  transition: background-color 0.5s ease;
}

.paginacion {
  display: flex;
  justify-content: center;
  font-size: 20px;
  font-weight: 600;
}
.paginacion a, .paginacion span {
  border-radius: 5px;
  padding: 3px 10px 3px 10px;
}
.paginacion--admin a {
  color: rgb(79.2012195122, 104.75, 130.2987804878);
}
.paginacion--admin a:hover {
  background-color: #D2DEEA;
}