body {
  background-color: #f2f2f0;
  font-family: Red Hat Text;
  display: grid;
  grid-template-rows: auto 1fr auto; /* header, contenido, footer */
  height: 100vh; /* Usa toda la altura de la ventana */
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.boton {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  padding: 0 15px 0 15px;
  height: 40px;
  border-radius: 50px;
  width: fit-content;
  text-align: center;
  color: white;
  background-color: hsl(14, 86%, 42%);
}
.boton:hover {
  cursor: pointer;
  background-color: hsl(14, 86%, 32%);
}
.boton--admin {
  background-color: #333333;
  color: #f0f4f8;
  border-radius: 15px;
}
.boton--admin:hover {
  background-color: initial;
  color: #333333;
  outline: 1px #333333 solid;
}

.modal {
  height: 100%;
  width: 100%;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(27, 26, 26, 0.9);
  z-index: 9999;
}
.modal__formulario {
  background-color: hsl(13, 31%, 94%);
  padding: 2rem;
}
.modal__botones {
  display: flex;
}
.modal--abierto {
  overflow: hidden;
}

.ruta_actual--admin {
  background-color: #D2DEEA;
  font-weight: 800;
}
.ruta_actual--admin a {
  color: #1f2933;
}
.ruta_actual--admin svg {
  transform: scale(1.3);
}

.filtro {
  height: 40px;
  margin-bottom: 50px;
  margin-top: 50px;
}
.filtro__boton {
  background-color: transparent;
  border: none;
  transition: transform 0.3s;
}
.filtro__boton:hover {
  cursor: pointer;
  transform: scale(1.3);
}
.filtro__select {
  height: 100%;
  min-width: 165px;
  border-radius: 5px;
  font-size: 20px;
  padding-left: 10px;
  padding-right: 10px;
  margin-right: 20px;
  border: 2px #dce3ec solid;
  transition: border 0.3s;
}
.filtro__select:hover {
  border: 2px #333333 solid;
  cursor: pointer;
}
.filtro__input {
  height: 100%;
  min-width: 165px;
  border-radius: 5px;
  font-size: 20px;
  padding-left: 10px;
}

.hidden {
  display: none;
}

.inicio {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.inicio__tienda {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 33%;
}
.inicio__usuario {
  display: flex;
  justify-content: space-between;
  width: 75%;
  margin-top: 5rem;
  text-align: center;
}
.inicio__elemento {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 40%;
}

.tienda {
  width: 100%;
  margin-top: 90px;
  display: grid;
  grid-template-columns: 2fr 1fr;
  place-content: center;
}
.tienda h2 {
  margin-bottom: 40px;
}
.tienda h3 {
  margin: 0;
}
.tienda h4, .tienda .precio-unidad, .tienda .cabecera-resumen p {
  color: hsl(7, 20%, 60%);
  font-weight: 400;
  margin-bottom: 10px;
}
.tienda p {
  font-weight: 600;
}
.tienda h3, .tienda h4, .tienda p {
  font-size: 16px;
}
.tienda img {
  display: block;
}
.tienda ul {
  width: 100%;
}
.tienda .productos {
  margin-left: 6.6%;
  margin-right: 6.6%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.tienda .contenedor-productos {
  display: grid;
  column-gap: 25px;
}
@media (min-width: 545px) {
  .tienda .contenedor-productos {
    grid-template-columns: repeat(2, 250px);
  }
  .tienda .resumen-orden {
    width: 41%;
  }
}
@media (min-width: 820px) {
  .tienda .contenedor-productos {
    grid-template-columns: repeat(3, 250px);
  }
}
@media (min-width: 1201px) {
  .tienda .contenedor {
    display: flex;
  }
}
.tienda .imagen-producto {
  width: 100%;
  border-radius: 10px;
}
.tienda .boton {
  width: 64%;
  height: 45px;
  margin-top: -22.5px;
  margin-left: 18%;
  margin-right: 18%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  gap: 10px;
}
.tienda .boton p {
  font-size: 12px;
}
.tienda input[type=number]::-webkit-inner-spin-button,
.tienda input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.tienda input[type=number] {
  -moz-appearance: textfield;
}
.tienda .boton input {
  width: 50%;
  text-align: center;
  background-color: hsl(14, 86%, 42%);
  border: none;
  height: 100%;
  color: white;
}
.tienda .boton-cantidad, .tienda .confirmar-orden {
  background-color: hsl(14, 86%, 42%);
  border: 1px solid hsl(14, 86%, 42%);
}
.tienda .boton-cantidad:hover {
  cursor: auto;
}
.tienda .boton-sumar, .tienda .boton-restar {
  background-color: hsl(14, 86%, 42%);
  border: 1px solid white;
  height: 15px;
  width: 15px;
  border-radius: 50px;
  display: grid;
  place-content: center;
}
.tienda .boton-sumar:hover, .tienda .boton-restar:hover {
  background-color: white;
  cursor: pointer;
}
.tienda .boton-sumar:hover img, .tienda .boton-restar:hover img {
  filter: invert(0.4) sepia() hue-rotate(-16deg) saturate(86) brightness(42);
}
.tienda .precio {
  margin-top: 10px;
  margin-bottom: 30px;
  color: hsl(14, 86%, 42%);
}
.tienda .carrito {
  background-color: hsl(20, 50%, 98%);
  margin-left: 2.45%;
  width: 385px;
  height: fit-content;
  border-radius: 10px;
}
.tienda .contenido-carrito, .tienda .carrito-vacio {
  display: grid;
  place-items: center;
}
.tienda .contador-carrito {
  margin-bottom: 30px;
}
.tienda .carrito h2 {
  margin-top: 30px;
  color: hsl(14, 86%, 42%);
  margin-left: 25px;
}
.tienda .texto-carrito-vacio, .tienda .precio-total {
  color: hsl(12, 20%, 44%);
}
.tienda .contenido-carrito {
  margin-left: 6.5%;
  margin-right: 6.5%;
  width: 87%;
}
.tienda .articulo-carrito, .tienda .precio-cantidad, .tienda .ecologico, .tienda .total-orden, .tienda .info {
  display: flex;
}
.tienda .articulo-carrito, .tienda .total-orden {
  justify-content: space-between;
}
.tienda .articulo-carrito {
  border-bottom: 1px solid lightgray;
  align-items: center;
  margin-bottom: 20px;
}
.tienda .precio-cantidad p {
  margin-bottom: 25px;
  margin-top: 15px;
}
.tienda .cantidad {
  margin-right: 15px;
  color: hsl(14, 86%, 42%);
}
.tienda .precio-unidad {
  margin-right: 5px;
}
.tienda .eliminar {
  width: 15px;
  height: 15px;
  border-radius: 50px;
  display: grid;
  place-content: center;
  border: 1px solid gray;
  background-color: white;
}
.tienda .eliminar:hover {
  cursor: pointer;
  border: 1px solid;
}
.tienda .eliminar:hover img {
  filter: brightness(0);
}
.tienda .total-orden {
  width: 100%;
  align-items: center;
}
.tienda .total-orden p {
  margin-top: 10px;
  margin-bottom: 30px;
}
.tienda .precio-final {
  font-weight: 700;
  font-size: 25px;
}
.tienda .ecologico {
  background-color: hsl(13, 31%, 94%);
  width: 100%;
  justify-content: center;
  gap: 10px;
}
.tienda .ecologico p {
  color: black;
}
.tienda .confirmar-orden {
  margin-top: 25px;
  margin-bottom: 25px;
  width: 100%;
  color: white;
}
.tienda .confirmar-orden:hover {
  color: white;
  background-color: hsl(14, 86%, 35%);
  border: 1px solid hsl(14, 86%, 35%);
}
.tienda .orden-confirmada {
  position: fixed;
  inset: 0;
  display: grid;
  place-items: center;
  background-color: hsla(17, 0%, 0%, 0.5);
}
.tienda .resumen-orden {
  max-width: 590px;
  max-height: 100%;
  width: 100%;
  background-color: white;
  border-radius: 10px;
  overflow: auto;
}
.tienda .cabecera-resumen {
  margin-top: 40px;
}
.tienda .cabecera-resumen h2 {
  margin-bottom: 0;
  font-size: 35px;
  margin-top: 20px;
}
.tienda .cabecera-resumen p {
  margin-bottom: 20px;
  margin-top: 0;
}
.tienda .resumen-precio {
  background-color: hsl(20, 50%, 98%);
}
.tienda .resumen-precio, .tienda .resumen-precio img {
  border-radius: 5px;
}
.tienda .resumen-orden button, .tienda .resumen-orden .resumen-precio, .tienda .cabecera-resumen {
  margin-right: 6.75%;
  margin-left: 6.75%;
  width: 86.5%;
}
.tienda .lista-confirmada {
  padding-top: 25px;
}
.tienda .lista-confirmada .articulo-carrito .precio-total {
  margin-top: 0;
  color: black;
}
.tienda .lista-confirmada, .tienda .total-orden {
  margin-right: 5.6%;
  margin-left: 5.6%;
}
.tienda .lista-confirmada .articulo-carrito, .tienda .total-orden {
  width: 88.8%;
}
.tienda .lista-confirmada .articulo-carrito {
  margin-bottom: 15px;
}
.tienda .info {
  gap: 15px;
}
.tienda .info img {
  width: 50px;
  height: 50px;
}
.tienda .datos-articulo p {
  margin-bottom: 15px;
}
.tienda .ocultar {
  display: none;
}
.tienda .articulo-seleccionado {
  outline: 3px solid hsl(14, 86%, 42%);
}

.checkout {
  display: flex;
  width: 100%;
  max-width: 12500px;
  justify-content: space-between;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: hsl(7, 20%, 60%);
  height: 8rem;
  width: 89%;
  padding-left: 5.5%;
  padding-right: 5.5%;
  margin-bottom: 50px;
}
.header__logo {
  font-size: 3rem;
  color: hsl(14, 65%, 9%);
  font-weight: 700;
}
.header__logo:hover {
  font-size: 3.1rem;
}
.header__navegacion {
  width: 50%;
}
.header__navegacion ul {
  display: flex;
  justify-content: end;
  list-style: none;
  gap: 2rem;
  padding: 0;
  width: 100%;
}
.header__enlace, .header__submit {
  color: white;
  font-size: 1.2rem;
  font-weight: 600;
}
.header__enlace:hover, .header__submit:hover {
  cursor: pointer;
  color: hsl(14, 86%, 42%);
}
.header__submit {
  background-color: transparent;
  border: none;
}
.header__admin {
  display: flex;
}
.header__admin .admin__fecha {
  display: flex;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: hsl(7, 20%, 60%);
  height: 60px;
  padding-left: 50px;
  padding-right: 50px;
  margin-top: 70px;
}

.login,
.registro {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.formulario {
  min-width: 350px;
  width: 30%;
  margin-bottom: 20px;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.formulario__label {
  margin-bottom: 10px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.5px;
  font-size: 1.25rem;
}
.formulario__input {
  border-radius: 0.25rem;
  height: 3rem;
  border: 1px solid hsl(7, 20%, 60%);
  padding-left: 1rem;
  font-size: 1.2rem;
}
.formulario__input::placeholder {
  color: hsl(14, 25%, 72%);
  font-size: 1.2rem;
}
.formulario__input--file {
  padding-left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 32px;
}
.formulario__input--img {
  display: flex;
  flex-direction: column;
}
.formulario__input-button {
  padding-left: 1rem;
  padding-right: 1rem;
  line-height: 3rem;
  width: 105px;
  flex: 0 0 auto;
  height: 100%;
  white-space: nowrap;
  border-right: 1px solid hsl(7, 20%, 60%);
}
.formulario__input-button:hover {
  cursor: pointer;
  font-weight: 600;
}
.formulario__input-text {
  flex: 1 1 auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.formulario__submit {
  height: 2.5rem;
  width: 10rem;
  text-transform: uppercase;
  color: white;
  font-weight: 600;
  background-color: hsl(14, 86%, 42%);
  border-radius: 50px;
  border: none;
}
.formulario__submit:hover {
  cursor: pointer;
  background-color: hsl(14, 86%, 32%);
}
.formulario__img {
  width: 200px;
  height: auto;
  border-radius: 15px;
}
.formulario--admin {
  width: 50%;
  max-width: 750px;
}
.formulario--admin .formulario__label {
  color: #1f2933;
}
.formulario--admin .formulario__input {
  border: 1px solid #dce3ec;
  color: #333333;
}
.formulario--admin .formulario__input::placeholder {
  color: rgb(146.8808510638, 152.7936170213, 164.6191489362);
  font-size: 1.2rem;
}
.formulario--admin .formulario__input-button {
  border-right: 1px solid #dce3ec;
  background-color: #dce3ec;
}
.formulario--admin .formulario__input-button:hover {
  background-color: rgb(192.6136363636, 209.25, 225.8863636364);
}
.formulario .boton {
  margin-bottom: 20px;
}

.acciones {
  display: flex;
  min-width: 325px;
  width: 30%;
  justify-content: space-between;
  gap: 5px;
}
.acciones--centrar {
  justify-content: center;
}
.acciones__enlace {
  color: hsl(14, 86%, 42%);
}
.acciones__enlace:hover {
  color: hsl(14, 65%, 9%);
}

.alerta {
  box-sizing: border-box;
  min-width: 325px;
  width: 30%;
  margin-bottom: 20px;
  padding: 10px 1rem;
  color: white;
  font-weight: 600;
  font-size: 17px;
  text-transform: uppercase;
  border-radius: 7px;
}
.alerta__error {
  background-color: rgb(169, 0, 0);
}
.alerta__exito {
  background-color: rgb(1.5384615385, 168.4615384615, 1.5384615385);
}

.tabla {
  border-collapse: collapse;
  margin-bottom: 50px;
}
.tabla__header, .tabla a {
  color: #333333;
}
.tabla__fila {
  height: 45px;
}
.tabla__celda--no-wrap {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.tabla__celda img {
  height: 100px;
  vertical-align: middle;
  border-radius: 8px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.tabla--admin .tabla__fila:hover:not(.tabla__fila--no-hover) {
  cursor: pointer;
}
.tabla--admin .tabla__celda {
  font-size: 25px;
  text-align: center;
  padding-left: 15px;
  padding-right: 15px;
}
.tabla--admin .tabla__celda--head:hover:not(.tabla__celda--no-hover) {
  background-color: rgb(192.6136363636, 209.25, 225.8863636364);
}
