.admin__layout {
  height: 100vh;
  display: grid;
  grid-template-columns: 300px 1fr;
  grid-template-rows: 100px 1fr;
  background-color: #f0f4f8;
  color: #6b7280;
  column-gap: 40px;
  margin-right: 50px;
}
.admin__layout h1 {
  color: #333333;
}
.admin__layout main {
  grid-column: 2;
  grid-row: 2/3;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header__admin {
  grid-column: 2;
  grid-row: 1/2;
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  margin-top: 50px;
  line-height: 50px;
}
.header__admin h2 {
  color: #1f2933;
  margin: 0;
}
.header__admin p {
  margin: 0;
}
