  display: flex;
  align-items: center;
}
.header__admin .admin__fecha svg {
  margin-left: 10px;
}

.admin__navegacion {
  width: 300px;
  grid-row: 1/3;
  grid-column: 1;
  display: grid;
  grid-template-rows: auto 1fr auto;
  height: 100vh;
  position: fixed;
  border-right: 2px solid #dce3ec;
}
.admin__navegacion ul {
  margin-top: 50px;
}
.admin__navegacion ul li {
  display: flex;
  height: 50px;
  font-size: 20px;
}
.admin__navegacion ul li a {
  display: inline-block;
  height: 100%;
  width: 100%;
  padding-left: 25px;
  line-height: 50px;
  color: #333333;
}
.admin__navegacion ul li a svg {
  margin-right: 10px;
}
.admin__navegacion ul li:hover {
  background-color: #e1e8f0;
  cursor: pointer;
  font-weight: 800;
}
.admin__navegacion ul li:hover svg {
  transform: scale(1.3);
  transition: transform 0.3s;
}
.admin__navegacion form {
  grid-row: 3/4;
}
.admin__navegacion form input {
  padding-left: 25px;
  height: 50px;
  width: 100%;
  text-align: left;
  font-size: 20px;
  background-color: transparent;
  border: none;
  color: #333333;
}
.admin__navegacion form input:hover {
  background-color: #e1e8f0;
  cursor: pointer;
  font-weight: 800;
}

