.admin__layout {
  height: 100vh;
  display: grid;
  grid-template-columns: 300px 1fr;
  grid-template-rows: 100px 1fr;
  background-color: #f0f4f8;
  color: #6b7280;
  column-gap: 40px;
  margin-right: 50px;
}
.admin__layout h1 {
  color: #333333;
}
.admin__layout main {
  grid-column: 2;
  grid-row: 2/3;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header__admin {
  grid-column: 2;
  grid-row: 1/2;
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  margin-top: 50px;
  line-height: 50px;
}
.header__admin h2 {
  color: #1f2933;
  margin: 0;
}
.header__admin p {
  margin: 0;
}
.header__admin .admin__fecha {
  display: flex;
  align-items: center;
}
.header__admin .admin__fecha svg {
  margin-left: 10px;
}

.admin__navegacion {
  width: 300px;
  grid-row: 1/3;
  grid-column: 1;
  display: grid;
  grid-template-rows: auto 1fr auto;
  height: 100vh;
  position: fixed;
  border-right: 2px solid #dce3ec;
}
.admin__navegacion ul {
  margin-top: 50px;
}
.admin__navegacion ul li {
  display: flex;
  height: 50px;
  font-size: 20px;
}
.admin__navegacion ul li a {
  display: inline-block;
  height: 100%;
  width: 100%;
  padding-left: 25px;
  line-height: 50px;
  color: #333333;
}
.admin__navegacion ul li a svg {
  margin-right: 10px;
}
.admin__navegacion ul li:hover {
  background-color: #e1e8f0;
  cursor: pointer;
  font-weight: 800;
}
.admin__navegacion ul li:hover svg {
  transform: scale(1.3);
  transition: transform 0.3s;
}
.admin__navegacion form {
  grid-row: 3/4;
}
.admin__navegacion form input {
  padding-left: 25px;
  height: 50px;
  width: 100%;
  text-align: left;
  font-size: 20px;
  background-color: transparent;
  border: none;
  color: #333333;
}
.admin__navegacion form input:hover {
  background-color: #e1e8f0;
  cursor: pointer;
  font-weight: 800;
}

.admin__layout .admin {
  display: grid;
  grid-template-columns: repeat(2, 475px);
  grid-template-rows: auto auto;
  gap: 20px;
  align-self: start;
  justify-content: center;
  margin-top: 30px;
}
.admin__layout .admin .ganancia_hoy {
  grid-column: 1/2;
  grid-row: 1/2;
}
.admin__layout .admin .pedidos_hoy {
  grid-column: 2/3;
  grid-row: 1/2;
}
.admin__layout .admin .ultimos_pedidos {
  grid-column: 1/3;
  grid-row: 2/3;
}
.admin__layout .admin .ganancia_hoy,
.admin__layout .admin .pedidos_hoy,
.admin__layout .admin .ultimos_pedidos {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px solid #dce3ec;
  border-radius: 15px;
  transition: outline 0.5s ease;
  padding-top: 10px;
}
.admin__layout .admin .ganancia_hoy h3,
.admin__layout .admin .pedidos_hoy h3,
.admin__layout .admin .ultimos_pedidos h3 {
  color: #333333;
  font-size: 40px;
}
.admin__layout .admin .ganancia_hoy span,
.admin__layout .admin .pedidos_hoy span,
.admin__layout .admin .ultimos_pedidos span {
  color: #333333;
  font-size: 50px;
}
.admin__layout .admin .ganancia_hoy,
.admin__layout .admin .pedidos_hoy {
  padding-bottom: 50px;
  text-align: center;
}
.admin__layout .admin .ganancia_hoy:hover,
.admin__layout .admin .pedidos_hoy:hover {
  outline: 2px solid #dce3ec;
}

.admin__layout .ventas h2 {
  color: rgb(76.5, 76.5, 76.5);
}
.admin__layout .ventas .mas_vendido {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px #dce3ec solid;
  border-radius: 10px;
  max-width: 700px;
  width: 100%;
  margin-bottom: 50px;
}
.admin__layout .ventas .mas_vendido p {
  margin: 0;
}
.admin__layout .ventas .mas_vendido__datos {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  font-size: 20px;
  text-align: center;
  margin-bottom: 25px;
}

.admin__layout .dashboard .resumen {
  max-width: 750px;
  width: 100%;
}
.admin__layout .dashboard .resumen__section {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  margin-top: 20px;
  margin-bottom: 50px;
  border-radius: 10px;
  border: 1px #dce3ec solid;
  transition: outline 0.3s ease-in;
}
.admin__layout .dashboard .resumen__section:hover {
  outline: 2px #dce3ec solid;
}
.admin__layout .dashboard .resumen__header {
  display: flex;
  position: absolute;
  margin-top: -21px;
}
.admin__layout .dashboard .resumen__header .filtro__boton {
  background-color: #f0f4f8;
  padding: 0;
  padding-right: -2px;
}
.admin__layout .dashboard .resumen__titulo {
  font-size: 20px;
  align-self: center;
  font-weight: 600;
  color: #1f2933;
  background-color: #f0f4f8;
  margin-right: 20px;
}
.admin__layout .dashboard .resumen__enlace {
  width: 100%;
  height: 100%;
  padding: 40px 0 40px 0;
}
.admin__layout .dashboard .resumen__lista {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.admin__layout .dashboard .resumen__lista--usuarios .resumen__datos:last-of-type {
  grid-column: 1/3;
}
.admin__layout .dashboard .resumen__datos {
  text-align: center;
  color: #1f2933;
  font-size: 20px;
}
.admin__layout .dashboard .resumen__datos-valor {
  font-size: 25px;
  font-weight: 600;
}
.admin__layout .dashboard .resumen .filtro {
  margin: 0;
}

.admin__layout .productos__acciones {
  width: 100%;
  max-width: 825px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}
.admin__layout .productos__enlace {
  align-self: center;
  display: flex;
  align-items: center;
  font-size: 20px;
  background-color: #333333;
  color: #f0f4f8;
  height: 40px;
  text-align: center;
  border-radius: 15px;
  padding: 0 10px 0 10px;
}
.admin__layout .productos__enlace:hover {
  background-color: initial;
  color: #333333;
  outline: 1px #333333 solid;
}
.admin__layout .productos .filtro {
  margin: 0;
}
.admin__layout .productos .tabla {
  width: 100%;
  max-width: 825px;
}
.admin__layout .productos .tabla__celda--head:last-of-type:hover {
  cursor: default;
  background-color: initial;
}
.admin__layout .productos .tabla__acciones {
  display: flex;
  justify-content: space-between;
  margin-top: -5px;
}
.admin__layout .productos .tabla__boton {
  border-radius: 15px;
  font-size: 15px;
  text-transform: uppercase;
  color: #f0f4f8;
}
.admin__layout .productos .tabla__boton:hover {
  cursor: pointer;
}
.admin__layout .productos .tabla__boton--editar {
  background-color: #333333;
}
.admin__layout .productos .tabla__boton--editar:hover {
  background-color: initial;
  color: #333333;
}
.admin__layout .productos .tabla__boton--borrar {
  background-color: hsl(14, 86%, 42%);
  border: 1px hsl(14, 86%, 42%) solid;
}
.admin__layout .productos .tabla__boton--borrar:hover {
  background-color: initial;
  color: hsl(14, 86%, 42%);
}